/* === COMPREHENSIVE DARK MODE THEME SYSTEM === */

/* Reset and Base Styles */
* {
  box-sizing: border-box;
}

:root {
  /* === LIGHT THEME (DEFAULT) === */
  
  /* Background Colors */
  --theme-bg-primary: #FDFDFB;
  --theme-bg-secondary: #ffffff;
  --theme-bg-tertiary: #f8fafc;
  --theme-bg-input: #ffffff;
  --theme-bg-input-focus: #ffffff;
  --theme-bg-input-disabled: #f3f4f6;
  
  /* Text Colors */
  --theme-text-primary: #292929;
  --theme-text-secondary: #757575;
  --theme-text-tertiary: #888888;
  --theme-text-muted: #666666;
  --theme-text-placeholder: #9ca3af;
  --theme-text-on-input: #1f2937;
  
  /* Border Colors */
  --theme-border-primary: rgba(0, 0, 0, 0.05);
  --theme-border-secondary: rgba(0, 0, 0, 0.1);
  --theme-border-input: #e5e7eb;
  --theme-border-input-focus: #3b82f6;
  --theme-border-input-error: #ef4444;
  --theme-border-input-success: #10b981;
  
  /* Glass Effect Variables */
  --theme-glass-bg: rgba(255, 255, 255, 0.06);
  --theme-glass-bg-hover: rgba(255, 255, 255, 0.09);
  --theme-glass-border: rgba(255, 255, 255, 0.1);
  --theme-glass-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  --theme-glass-backdrop: blur(16px) saturate(180%);
  
  /* Interactive States */
  --theme-hover-bg: rgba(0, 0, 0, 0.05);
  --theme-active-bg: rgba(0, 0, 0, 0.1);
  --theme-focus-ring: rgba(59, 130, 246, 0.2);
  
  /* Brand Colors */
  --theme-brand-primary: #1a8917;
  --theme-brand-secondary: #156c14;
  --theme-accent: #e53935;
  
  /* Status Colors */
  --theme-success: #10b981;
  --theme-warning: #f59e0b;
  --theme-error: #ef4444;
  --theme-info: #3b82f6;
}

/* === DARK THEME === */
[data-theme="dark"] {
  /* Background Colors */
  --theme-bg-primary: #0a0a0a;
  --theme-bg-secondary: #111111;
  --theme-bg-tertiary: #1a1a1a;
  --theme-bg-input: #1f2937;
  --theme-bg-input-focus: #374151;
  --theme-bg-input-disabled: #1f2937;
  
  /* Text Colors */
  --theme-text-primary: #f9fafb;
  --theme-text-secondary: #d1d5db;
  --theme-text-tertiary: #9ca3af;
  --theme-text-muted: #6b7280;
  --theme-text-placeholder: #6b7280;
  --theme-text-on-input: #f9fafb;
  
  /* Border Colors */
  --theme-border-primary: rgba(255, 255, 255, 0.08);
  --theme-border-secondary: rgba(255, 255, 255, 0.15);
  --theme-border-input: #374151;
  --theme-border-input-focus: #60a5fa;
  --theme-border-input-error: #f87171;
  --theme-border-input-success: #34d399;
  
  /* Glass Effect Variables */
  --theme-glass-bg: rgba(255, 255, 255, 0.03);
  --theme-glass-bg-hover: rgba(255, 255, 255, 0.06);
  --theme-glass-border: rgba(255, 255, 255, 0.08);
  --theme-glass-shadow: 0 4px 20px rgba(0, 0, 0, 0.25);
  --theme-glass-backdrop: blur(16px) saturate(180%);
  
  /* Interactive States */
  --theme-hover-bg: rgba(255, 255, 255, 0.08);
  --theme-active-bg: rgba(255, 255, 255, 0.15);
  --theme-focus-ring: rgba(96, 165, 250, 0.3);
  
  /* Brand Colors (adjusted for dark mode) */
  --theme-brand-primary: #22c55e;
  --theme-brand-secondary: #16a34a;
  --theme-accent: #ef4444;
  
  /* Status Colors */
  --theme-success: #34d399;
  --theme-warning: #fbbf24;
  --theme-error: #f87171;
  --theme-info: #60a5fa;
}

/* === GLOBAL FORM INPUT STYLING === */
/* This ensures NO input ever loses its background */

input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="search"],
input[type="tel"],
input[type="number"],
input[type="date"],
input[type="datetime-local"],
input[type="month"],
input[type="time"],
input[type="week"],
textarea,
select {
  background-color: var(--theme-bg-input) !important;
  color: var(--theme-text-on-input) !important;
  border: 1px solid var(--theme-border-input) !important;
  border-radius: 8px;
  padding: 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  transition: all 0.2s ease;
  font-family: inherit;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="password"]:focus,
input[type="url"]:focus,
input[type="search"]:focus,
input[type="tel"]:focus,
input[type="number"]:focus,
input[type="date"]:focus,
input[type="datetime-local"]:focus,
input[type="month"]:focus,
input[type="time"]:focus,
input[type="week"]:focus,
textarea:focus,
select:focus {
  background-color: var(--theme-bg-input-focus) !important;
  border-color: var(--theme-border-input-focus) !important;
  outline: none !important;
  box-shadow: 0 0 0 3px var(--theme-focus-ring) !important;
}

input::-moz-placeholder, textarea::-moz-placeholder {
  color: var(--theme-text-placeholder) !important;
  opacity: 1;
}

input::placeholder,
textarea::placeholder {
  color: var(--theme-text-placeholder) !important;
  opacity: 1;
}

input:disabled,
textarea:disabled,
select:disabled {
  background-color: var(--theme-bg-input-disabled) !important;
  color: var(--theme-text-muted) !important;
  cursor: not-allowed;
  opacity: 0.6;
}

/* === FORM VALIDATION STATES === */
input:invalid:not(:focus):not(:-moz-placeholder), textarea:invalid:not(:focus):not(:-moz-placeholder) {
  border-color: var(--theme-border-input-error) !important;
  background-color: var(--theme-bg-input) !important;
}
input:invalid:not(:focus):not(:placeholder-shown),
textarea:invalid:not(:focus):not(:placeholder-shown) {
  border-color: var(--theme-border-input-error) !important;
  background-color: var(--theme-bg-input) !important;
}

input.error,
textarea.error {
  border-color: var(--theme-border-input-error) !important;
  background-color: var(--theme-bg-input) !important;
}

input.success,
textarea.success {
  border-color: var(--theme-border-input-success) !important;
  background-color: var(--theme-bg-input) !important;
}

/* === AUTO-ADJUSTING UTILITY CLASSES === */
.theme-text-primary { color: var(--theme-text-primary) !important; }
.theme-text-secondary { color: var(--theme-text-secondary) !important; }
.theme-text-tertiary { color: var(--theme-text-tertiary) !important; }
.theme-text-muted { color: var(--theme-text-muted) !important; }

.theme-bg-primary { background-color: var(--theme-bg-primary) !important; }
.theme-bg-secondary { background-color: var(--theme-bg-secondary) !important; }
.theme-bg-tertiary { background-color: var(--theme-bg-tertiary) !important; }

.theme-border-primary { border-color: var(--theme-border-primary) !important; }
.theme-border-secondary { border-color: var(--theme-border-secondary) !important; }

/* Enhanced Glass Effect Classes */
.theme-glass {
  background: var(--theme-glass-bg) !important;
  backdrop-filter: var(--theme-glass-backdrop);
  -webkit-backdrop-filter: var(--theme-glass-backdrop);
  border: 1px solid var(--theme-glass-border) !important;
  box-shadow: var(--theme-glass-shadow);
}

.theme-glass-hover:hover {
  background: var(--theme-glass-bg-hover) !important;
}

/* === SPECIFIC COMPONENT OVERRIDES === */

/* Search bars */
.centerSearchInput,
input[placeholder*="Search"],
input[placeholder*="search"] {
  background-color: var(--theme-bg-input) !important;
  color: var(--theme-text-on-input) !important;
  border: 1px solid var(--theme-border-input) !important;
}

/* Article composer inputs */
.titleInput,
textarea[placeholder*="Title"],
textarea[placeholder*="title"] {
  background-color: transparent !important;
  color: var(--theme-text-primary) !important;
  border: none !important;
}

/* Comment textareas */
textarea[placeholder*="thoughts"],
textarea[placeholder*="comment"],
textarea[placeholder*="reflection"] {
  background-color: var(--theme-bg-input) !important;
  color: var(--theme-text-on-input) !important;
  border: 1px solid var(--theme-border-input) !important;
}

/* Profile form inputs */
input[id*="firstName"],
input[id*="lastName"],
input[id*="email"],
input[id*="username"],
input[id*="password"] {
  background-color: var(--theme-bg-input) !important;
  color: var(--theme-text-on-input) !important;
  border: 1px solid var(--theme-border-input) !important;
}

/* === BROWSER AUTOFILL OVERRIDE === */
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px var(--theme-bg-input) inset !important;
  -webkit-text-fill-color: var(--theme-text-on-input) !important;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

[data-theme="dark"] input:-webkit-autofill,
[data-theme="dark"] input:-webkit-autofill:hover,
[data-theme="dark"] input:-webkit-autofill:focus,
[data-theme="dark"] input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px #1f2937 inset !important;
  -webkit-text-fill-color: #f9fafb !important;
}

/* === DROPDOWN AND SELECT STYLING === */
select {
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6,9 12,15 18,9'%3e%3c/polyline%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 0.75rem center;
  background-size: 1em;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  padding-right: 2.5rem;
}

[data-theme="dark"] select {
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='%23f9fafb' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6,9 12,15 18,9'%3e%3c/polyline%3e%3c/svg%3e");
}

/* === SCROLLBAR STYLING === */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--theme-bg-tertiary);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: var(--theme-border-secondary);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--theme-text-tertiary);
}

/* === SMOOTH TRANSITION ON THEME CHANGE === */
html {
  transition: background-color 0.3s ease, color 0.3s ease;
}

body {
  background-color: var(--theme-bg-primary);
  color: var(--theme-text-primary);
  transition: background-color 0.3s ease, color 0.3s ease;
}

*,
*::before,
*::after {
  transition: background-color 0.3s ease, border-color 0.3s ease, color 0.3s ease;
}

/* === ACCESSIBILITY === */
@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
    animation: none !important;
  }
}

/* === HIGH CONTRAST MODE === */
@media (prefers-contrast: high) {
  :root {
    --theme-border-input: #000000;
    --theme-border-input-focus: #0066cc;
  }
  
  [data-theme="dark"] {
    --theme-border-input: #ffffff;
    --theme-border-input-focus: #66ccff;
  }
} 